import React, { Component } from "react";
import { Link } from "gatsby";
import * as styles from "../styles/chunk.module.scss"
import { PersonalTraining } from "../components/icons"

interface ChunkProps {
    header?: string,
    summary?: string,
    image: any,
    flip: boolean,
    flipImage?: boolean,
    arrange: number,
    pathStyle: number,
    id: string,
    offset?: {
        containerOffset?: {
            x: string,
            y: string
        },
        imageOffset?: {
            x: string,
            y: string
        }
    },
    icon?: any,
    link: boolean
}

const svgPath = "M46.5208829,8.34282794 C48.858553,8.20864403 51.2034907,8.27998212 53.528681,8.55602035 L883.005747,107.028557 C893.974418,108.330718 903.376881,113.955537 909.711636,121.996983 C916.050731,130.043937 919.317913,140.510831 918.008594,151.487715 L866.819419,563.710956 C865.660846,573.040786 861.331392,581.337389 854.95103,587.516175 C848.570668,593.694962 840.139398,597.755932 830.777193,598.614496 L85.6914394,666.942838 C74.6918999,667.951554 64.3248227,664.406332 56.4554513,657.859045 C48.5860798,651.311758 43.2144142,641.762407 42.2056977,630.762867 C42.1641612,630.309933 42.1303531,629.856324 42.1042882,629.402237 L8.8788723,50.5693485 C8.24588357,39.5418056 12.1426147,29.3016698 18.9544788,21.6601726 C25.7663429,14.0186754 35.4933399,8.97581668 46.5208829,8.34282794 Z"

const svgPath2 = "M79.5052933,16.6071991 C87.1322517,10.2837377 97.1542152,6.84897983 107.79164,7.77833305 L806.348274,68.8086889 C815.257906,69.5870911 823.331695,73.3460572 829.530831,79.118313 C835.729967,84.8905688 840.054452,92.6761143 841.465476,101.507676 L912.503978,546.135539 C914.203087,556.770213 911.430788,567.086803 905.575044,575.169503 C899.719299,583.252202 890.780109,589.10101 880.145435,590.800119 C879.262491,590.941188 878.374957,591.05185 877.484405,591.13191 L50.1665736,665.507183 C39.4402781,666.471469 29.3385858,662.996776 21.6773959,656.599438 C14.0162059,650.202099 8.79551836,640.882116 7.83123216,630.15582 C7.61125051,627.708841 7.62298503,625.246549 7.86627925,622.801778 L65.5889768,42.7682856 C66.6463816,32.1428251 71.8783349,22.9306604 79.5052933,16.6071991 Z"

const Chunk = ({ header, summary, image, flip, flipImage, arrange, pathStyle, id, offset, icon, link }: ChunkProps) => {

    const path = pathStyle === 1 ? svgPath : svgPath2
    const pathId = (pathStyle === 1 ? "Rectangle" : "Rectangle2") + id
    const pathIdRef = (pathStyle === 1 ? "#Rectangle" : "#Rectangle2") + id
    const IconComponent = React.createElement("div", { className: `absolute ${flip ? 'right-[-100px]' : 'left-[-100px]'} top-[-20px] icon bg-yellow rounded-full w-[70px] h-[70px] grid place-items-center ${styles.icon}` }, icon)

    return (
        <Link to={link ? `/${id}` : ''} className="group">
            <section
                className={flip ? styles.chunkFlipped : styles.chunk}
                style={{
                    zIndex: arrange,
                    marginTop: offset?.containerOffset?.y,
                    marginLeft: offset?.containerOffset?.x
                }}
            >
                <svg
                    className={flip ? styles.svgFlipped : undefined}
                    width="940px"
                    height="670px"
                    viewBox="0 0 940 670"
                    style={{
                        clipPath: `url(#chunkClipPath${id})`,
                        marginLeft: offset?.imageOffset?.x,
                        marginTop: offset?.imageOffset?.y
                    }}
                >
                    <defs>
                        <path d={path} id={pathId} fill="none"></path>
                        <clipPath id={"chunkClipPath" + id}>
                            <use href={pathIdRef} stroke="#FECF09" strokeWidth="16" fill="#6A6A6A" />
                        </clipPath>
                    </defs>
                    <image xlinkHref={image} className={`w-[110%] ${styles.chunkImage} ${flipImage ? 'scale-x-[-1] origin-center':''}`} />
                    {/* <div className='bg-[url("https://www.alliancefitnesscenter.com/wp-content/uploads/2020/09/IMG_1394.jpg")] bg-cover'/> */}
                    <use href={pathIdRef} stroke="#FECF09" strokeWidth="26" fill="none" />
                </svg>

                {
                    summary || header &&
                    <section className={flip ? styles.infoFlipped : styles.info}>
                        {icon && IconComponent}

                        <header>
                            <h3 className="font-bold text-2xl">{header}</h3>
                        </header>

                        <p className="leading-5 mb-5 sm:text-sm md:text-base md:leading-5 lg:text-lg lg:leading-6">{summary}</p>


                        <button className="bg-black text-yellow border-none rounded-full px-5 py-2 font-bold group-hover:bg-yellow group-hover:text-black transition duration-500">
                            LEARN MORE
                        </button>
                    </section>
                }
            </section>
        </Link>
    )
}

export default Chunk
